$(document).ready(function() {
// Слайдер
$('.slider').slick({
    responsive: [
        {
            breakpoint: 5000,
            settings: "unslick",
        },
        {
            breakpoint: 425,
            settings: {
                dots: true,
                infinite: true,
                speed: 300,
                slidesToShow: 1,
                autoplay: true,
                autoplaySpeed: 3000,
                arrows: false,
                centerPadding: '-20px',
                adaptiveHeight: true,
            }
        }
    ]
});

// таймер
let minutes = $('#minutes');
let seconds = $('#seconds');


function timerSeconds() {

    let second = parseInt(seconds.text());

    setInterval(() => {
        if (second > 0) {
            second--;
        } else {
            second = 59;
        }

        if (second < 10) {
            seconds.text('0' + second);
        } else {
            seconds.text(second);
        }
        timerMinutes();
    }, 1000)

}

function timerMinutes() {
    let minut = parseInt(minutes.text());
    if (parseInt(seconds.text()) === 59) {
        minut--;
        if (minut < 0) {
            minut = 9;
        }

        if (minut < 10) {
            minutes.text('0' + minut);
        } else {
            minutes.text(minut);
        }

    }
}

timerSeconds();
})
// фон
// let ballsHeight = parseInt($('.window').css('height')) + 82;
$('.balls').css('min-height', $('.window').css('height'));
$('.balls-left').parallax({imageSrc: './../img/bg-l.png'});
$('.balls-right').parallax({imageSrc: './../img/bg-r.png'});
